<script type="es6">
import "element-ui/lib/theme-chalk/display.css";
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      lpBalance: 0,
      coinBalance: 0,
      isApprove: false,
      stakeAmount: 0,
      dialogTableVisible: false,
      hmdApprove: false,
      joinEstablish: "",
      creationCode: "",
      dogeApprove: false,
      drawer: false,
      direction: "ltr",
      link:window.location.href
    };
