    ...mapActions("main", [
      "add_DOGESpool",
      "add_lpCoinContract",
      "add_dogCoinContract",
    ]),
    randomNum: function () {
      var code = "";
      for (var i = 1; i <= 6; i++) {
        const num = Math.floor(Math.random() * 10);
        code += num;
      }
      this.creationCode = code;
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
      localStorage.setItem("lang", lang);
    },

    // 复制成功时的回调函数
