      var account = _this.dogesInfo.account;
      var code = _this.dogesInfo.inviteCode || _this.dogesInfo.referrer;
      if (!_this.isApprove) {
        _this.$message({
          showClose: true,
          message: _this.$t("message.noApprove"),
          type: "error",
        });
        return;
      }

      if (!code || code == "--") {
        code = _this.$route.query.invite
          ? _this.$route.query.invite.toString()
          : 1;
      }
      if (Number(_this.stakeAmount) > Number(_this.lpBalance)) {
        _this.$message({
          showClose: true,
          message: _this.$t("message.noLp"),
          type: "error",
        });
