        .catch((err) => {
          loading.close();
          _this.$message({
            showClose: true,
            message: err.message,
            type: "error",
          });
          console.log(err);
        });
    },
    // 是否授权
    isApproveFun: function () {
      if (this.dogesInfo) {
        var account = this.dogesInfo.account;
        var address = this.dogesInfo.address;

        this.lpCoinContract.methods
          .allowance(account, address)
          .call()
          .then((res) => {
            var a = web3.utils.fromWei(res, "ether");
