    //获取当前币种余额
    getLpBalance: function () {
      var account = this.dogesInfo.account;
      if (account) {
        this.lpCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            this.lpBalance = res;
          });
      }
    },
