    //   _this.joinEstablish = _this.$route.query.invite;
    // }
    var getData = () => {
      this.add_DOGESpool();
      this.add_lpCoinContract();
      this.add_dogCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (
          !this.dogCoinContract ||
          !this.lpCoinContract ||
          !this.poolContract ||
          !this.dogesInfo
        ) {
          getData();
        } else {
          this.getLpBalance();
          this.getDOGBalance();
          console.log(this.dogesInfo);
          this.isApproveFun();
          this.dogisApproveFun();
          // if (!_this.dogesInfo.inviteCode && !_this.dogesInfo.referrer) {
          _this.joinEstablish = _this.$route.query.code
