          //   var invite =
          //     _this.dogesInfo.inviteCode || _this.hmdCntmLpInfo.referrer;
          //   _this.getUrl(invite);
          // }

          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 60000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
