.earnings {
  background: url(../../assets/img/insvtBg.png) no-repeat #062f2c;
  margin-bottom: 60px;
  padding: 90px 30px 20px;
  min-height: 325px;
  overflow: hidden;
}
.Extract {
  border: none;
  background: url(../../assets/img/btn-l.png) no-repeat;
  color: #000e12;
  background-size: 100%;
  padding: 10px 20px;
  font-size: 20px;
  float: right;
  margin-top: 14px;
}
.marginB20 {
  margin-bottom: 20px;
}
.marginB30 {
  margin-bottom: 30px;
}
.marginB10 {
  margin-bottom: 10px;
}
.dog {
  font-size: 16px;
  color: #daffed;
}
.dog span {
  display: block;
  font-size: 18px;
  text-align: center;
  font-size: 24px;
  margin-top: 16px;
  border-bottom: 1px solid #c7ebda;
  padding: 10px 0;
  color: #bff408;
}
.info,
.invest {
  background: url(../../assets/img/insvtBg.png) no-repeat #062f2c;
  background-size: 100% 50px;
  margin-bottom: 60px;
  padding: 90px 30px 20px;
}
.info h2 {
  font-size: 22px;
  color: #f99800;
  text-align: center;
  margin-bottom: 30px;
}
.food ul {
  list-style: none;
}
.food li {
  margin-bottom: 21px;
}
.food li span {
  float: right;
}
.share {
  display: block;
  width: 50%;
  margin: 48px auto 0;
  background:url(../../assets/img/z.png) no-repeat;
  background-size:100% ;
  padding: 10px 0;
  border: none;
  color: #000E12;
  font-size: 18px;
}
.put {
  border: 2px solid #A8DD53;
}
.put input {
  width: 80%;
  background: #021312;
  border: none;
  color: #fff;
  font-size: 24px;
  padding: 8px 15px;
  box-sizing: border-box;
}
.put button {
  background:  linear-gradient(0deg, #155121 0%, #1F5D21 10%, #50CF13 100%);
  border: none;
  height: 44px;
  line-height: 44px;
  float: right;
  width: 20%;
  color: #fff;
  font-size: 18px;
}
.invest .share {
  width: auto;
  margin: 0;
  display: inline-block;
  padding: 0;
}
.invest .share a {
  display: block;
  color: #000E12;
  padding: 10px 10px;
}
.invest .btn {
  width: 98%;
  margin: 40px auto 4px;
}
.all {
  border: none;
  padding: 10px 20px;
  color: #fff;
  font-size: 18px;
background: linear-gradient(0deg, #155121 0%, #1F5D21 10%, #50CF13 100%);
  float: right;
  border-radius: 20px;
}
.con .content {
  display: flex;
  justify-content: center;
  align-items: center;
}

.con .content input {
  height: 50px;
  border: 0;
  background: #e9eefc;
  width: 100%;
  box-sizing: border-box;
  padding: 0 10px;
  border-radius: 10px;
  font-size: 16px;
  color: #000000;
}
.con p {
  margin-bottom: 10px;
}
.con .popfooter {
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.con .popfooter .btn {
  height: 45px;
  width: 40%;
  color: #fff;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
}

.con .popfooter .cancel {
  background: #62698e;
}

.con .popfooter .confirm {
  background: #007bec;
}

.sc {
  position: absolute;
  padding: 0 10px;
  background: #ff861e;
  height: 50px;
  text-align: center;
  line-height: 50px;
  color: #fff;
  border-radius: 0 10px 10px 0;
  right: 20px;
}
.hidden-sm-and-show {
  display: none;
}
.drawerList a {
  display: block;
  text-align: center;
  font-size: 16px;
  color: #4e4f57;
  width: 95%;
  margin: 20px auto;
  padding: 10px 0 20px;
  border-bottom: 1px solid #f1f2f3;
}
.hbox {
  height: 160px;
}
.hbox p{
  margin-bottom: 5px;
}
@media only screen and (max-width: 767px) {
  .logo{width: 200px;}
  .head a{
    font-size: .8em;
    border: 1px solid #8ed343;
    background: #000;
    padding: 0 10px;
    line-height: 30px;
    height: 30px;
  }
  .home{
        background: url(../../assets/img/min-bg.png) no-repeat #05211f;
        background-size:100% ;
  }
  .pet .bg-l{
  margin: 10px 0;
      background-size: 100% 100%;
}
  .web {
    width:92%;
    margin: 0 auto;
    overflow: hidden;
  }

  .hidden-sm-and-show {
    display: block;
  }
  /* .head a {
    float: right;
  } */
  .introduce .title{
    line-height: 1em;
    font-size: 2.2em;
    margin-bottom: 20px;
  }
  .gou{height:20em}
  .gou{
    width: 90%;
  }
  .introduce {
    padding-top: 2rem;
  }
  .introduce .title {
    top: -1.5rem;
  }
  /* .introduce h2{margin: 0.8em auto;} */
  .traffic div p {
    font-size: 18px;
  }
  .traffic .float-box {
    margin-left: 12px;
  }
  .btnbox a {
    padding: 12px 15px;
    margin: 0 10px;
    font-size: 1rem;
  }
  .food h1 {
    font-size: 34px;
    margin-bottom: 0px;
  }
  .food .el-row:first-child {
    margin-bottom: 50px;
  }
  .earnings,
  .info,
  .invest {
    min-height: auto;
    background: url(../../assets/img/insvtBg.png) no-repeat #062f2c;
    background-size:100% ;
    /* padding:20px; */
  }
  .trad {
    background-size: 100%;
  }
}
@media (min-width: 768px) and (max-width: 992px) {
  .web {
    width: 95%;
    margin: 0 auto;
    overflow: hidden;
  }
  .hidden-sm-and-show {
    display: block;
  }
  .head a {
    float: right;
  }

}
@media (min-width: 992px) and (max-width: 1142px) {
  .web {
    width: 95%;
    margin: 0 auto;
    overflow: hidden;
  }
}
</style>



