      oIframe.style.height = Number(val) + "px"; // '120'是页面布局调整，可去除
    },
  },
    destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  mounted() {
    var _this = this;
    var lang = localStorage.getItem("lang");
    this.changeLanguage(lang || "en");
    // 旋转星空  开始
    window.onresize = function () {
      // 定义窗口大小变更通知事件
      _this.screenHeight = document.documentElement.clientHeight; // 窗口高度
    };
    var canvas = document.getElementById("canvas");
    var ctx = canvas.getContext("2d");
    var w = (canvas.width = window.innerWidth);
    var h = (canvas.height = window.innerHeight);
    var hue = 0;
    var stars = [];
    var count = 0;
    var maxStars = 1100; // 星星数量

    var canvas2 = document.createElement("canvas");
    var ctx2 = canvas2.getContext("2d");
    canvas2.width = 100;
    canvas2.height = 100;
    var half = canvas2.width / 2;
    var gradient2 = ctx2.createRadialGradient(half, half, 0, half, half, half);
    gradient2.addColorStop(0.025, "#fff");
    gradient2.addColorStop(0.1, "#03211f");
    gradient2.addColorStop(0.25, "#000");
    gradient2.addColorStop(1, "transparent");

    ctx2.fillStyle = gradient2;
    ctx2.beginPath();
    ctx2.arc(half, half, half, 0, Math.PI * 2);
    ctx2.fill();

    // End cache

    function random(min, max) {
      if (arguments.length < 2) {
        max = min;
        min = 0;
      }

      if (min > max) {
        var hold = max;
        max = min;
        min = hold;
      }

      return Math.floor(Math.random() * (max - min + 1)) + min;
    }

    function maxOrbit(x, y) {
      var max = Math.max(x, y);
      var diameter = Math.round(Math.sqrt(max * max + max * max));
      return diameter / 2;
      // 星星移动范围，值越大范围越小，
    }

    var Star = function () {
      this.orbitRadius = random(maxOrbit(w, h));
      this.radius = random(60, this.orbitRadius) / 7;
      // 星星大小
      this.orbitX = w / 2;
      this.orbitY = h / 2;
      this.timePassed = random(0, maxStars);
      this.speed = random(this.orbitRadius) / 60000;
      // 星星移动速度
      this.alpha = random(2, 10) / 10;

      count++;
      stars[count] = this;
    };

    Star.prototype.draw = function () {
      var x = Math.sin(this.timePassed) * this.orbitRadius + this.orbitX;
      var y = Math.cos(this.timePassed) * this.orbitRadius + this.orbitY;
      var twinkle = random(10);

      if (twinkle === 1 && this.alpha > 0) {
        this.alpha -= 0.05;
      } else if (twinkle === 2 && this.alpha < 1) {
        this.alpha += 0.05;
      }

      ctx.globalAlpha = this.alpha;
      ctx.drawImage(
        canvas2,
        x - this.radius / 2,
        y - this.radius / 2,
        this.radius,
        this.radius
      );
      this.timePassed += this.speed;
    };

    for (var i = 0; i < maxStars; i++) {
      /* eslint-disable no-new */
      new Star();
    }

    function animation() {
      ctx.globalCompositeOperation = "source-over";
      ctx.globalAlpha = 1; // 尾巴
      ctx.fillStyle = "#05211f";
      ctx.fillRect(0, 0, w, h);

      ctx.globalCompositeOperation = "lighter";
      for (var i = 1, l = stars.length; i < l; i++) {
        stars[i].draw();
      }

      window.requestAnimationFrame(animation);
    }

    animation();
    // 旋转星空  结束

    if (!window.ethereum) {
      this.$notify({ type: "danger", message: this.$t("message.noMask") });
      return;
    } else {
      var web3 = new Web3(window.ethereum);
      web3.eth.net.getId().then((id) => {
        if (id != 56) {
          this.$notify({
            type: "danger",
            message: "Please switch the network!",
          });
          return;
        }
      });
    }
    var getData = () => {
      this.add_CBNpool();
      this.add_CbnCoinContract();
      this.add_lpCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (
          !this.CbnCoinContract ||
          !this.CBNpoolInfo ||
          !this.CbnCoinBalance ||
          !this.CBNpool ||
          !this.CBNlpInfo
        ) {
          getData();
        } else {
          this.IsApproveFun();
            getData();
            // isRedeemable
            var timestamp = Date.parse(new Date()) / 1000;
            this.isRedeemable = timestamp - Number(this.CBNpoolInfo.info[7]) > 0 ? true:false;
            this.countDown =(Number(this.CBNpoolInfo.info[11]) + 86400 - timestamp) *1000
          console.log(this.CBNpoolInfo,timestamp,this.CBNpoolInfo.info[7])
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 60000);
  },
  methods: {
    ...mapActions("main", [
      "add_CBNpool",
      "add_CbnCoinContract",
      "add_lpCoinContract",
    ]),
     LanguageClick: function () {
      this.isShowNav = !this.isShowNav;
    },
    colsBox(){
      this.listTile=false;
    },
    son( idx) {
      this.index = idx;
    },
    addClass(val) {
      document.getElementById(val).style.color = "black";
      // document.getElementById('register').style.color = 'black'
    },
    deleteClass(val) {
      document.getElementById(val).style.color = "";
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
      this.$router.push({
        name: this.$route.name,
        params: { lang: this.lang },
      });
      
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
      this.isShowNav = false;
    },
    zb:function(type,num){
       var a = num.substring(0,num.length-21);
      if(type==2){
       a = (200 - Number(a)).toString();
       return a / 200;
      }else{
        return a / 1000;
      }
      
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(36, 42)
        : addr;
    },
    ApproveFun: function () {
      if (this.CBNpoolInfo == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.CBNpoolInfo.address;
     var loading = this.$loading({
        lock: true,
        text: "Padding....",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.5)",
      });
      var account = this.CBNpoolInfo.account;
      this.CbnCoinContract.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          // console.log("授权=====", res);
          loading.close();
          this.$message({
            showClose: true,
            message: 'ok',
            type: "error",
          });
          // this.$toast.success(this.$t("message.accredit"));
          this.Approve = true;
        })
        .catch((err) => {
          loading.close();
           this.$message({
            showClose: true,
            message: err.message,
            type: "error",
          });
          console.log(err);
        });
    },
    // 是否授权
    IsApproveFun: function () {
      if (this.CBNpoolInfo) {
        var account = this.CBNpoolInfo.account;
        var address = this.CBNpoolInfo.address;
        this.CbnCoinContract.methods
          .allowance(account, address)
          .call()
          .then((res) => {
            var a = web3.utils.fromWei(res, "ether");
            if (a > 0) {
              this.Approve = true;
            }
          })
          .catch((err) => {
            // this.$toast.clear();
            // this.$toast(err.message);
            console.log(err);
          });
      }
    },
    stake: function () {
      var _this = this;
      if (this.CBNpoolInfo == null) {
        _this.$message({
          showClose: true,
          message: _this.$t("message.nolLinked"),
          type: "error",
        });
        return;
      }

      var account = _this.CBNpoolInfo.account;
      // var code = _this.CBNpoolInfo.inviteCode || _this.CBNpoolInfo.referrer;
      if (!_this.Approve) {
        _this.$message({
          showClose: true,
          message: _this.$t("message.noApprove"),
          type: "error",
        });
        return;
      }

      // if (!code || code == "--") {
      //   code = _this.$route.query.invite
      //     ? _this.$route.query.invite.toString()
      //     : 1;
      // }
      if (Number(_this.stakeAmount) > Number(_this.CbnCoinBalanc)) {
        _this.$message({
          showClose: true,
          message: _this.$t("message.noLp"),
          type: "error",
        });
        return;
      }
      if (Number(_this.CBNpoolInfo.BNBbalance) <= 0) {
        _this.$message({
          showClose: true,
          message: _this.$t("message.noGas"),
          type: "error",
        });
        return;
      }
      // if (!code || code == "--") {
      //   _this.$message({
      //     showClose: true,
      //     message: _this.$t("message.noCode2"),
      //     type: "error",
      //   });
      //   return;
      // }
      if (Number(_this.stakeAmount) <= 0) {
        _this.$message({
          showClose: true,
          message: _this.$t("message.noAmount"),
          type: "error",
        });
        return;
      }
      var loading = _this.$loading({
        lock: true,
        text: "Padding....",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.5)",
        stroke: "#c8291c",
      });
      // var monye = (_this.stakeAmount * 10 ** 18).toString();
      console.log(web3.utils.toWei(_this.stakeAmount, "shannon"))
      this.CBNpool.methods
        .investIn('1', web3.utils.toWei(_this.stakeAmount, "shannon"))
        .send({ from: account })
        .then((res) => {
          console.log(res);
          loading.close();
          this.$message({
            showClose: true,
            message: "success",
            type: "success",
          });
          _this.add_CbnCoinContract();
          _this.add_CBNpool();

          _this.stakeAmount = 0;
        })
        .catch((err) => {
          loading.close();
          _this.$message({
            showClose: true,
            message: err.message,
            type: "error",
          });
          console.log(err);
        });
    },
    getReward: function () {
      var _this = this;
      if (this.CBNpoolInfo == null) {
        _this.$message({
          showClose: true,
          message: _this.$t("message.nolLinked"),
          type: "error",
        });
        return;
      }
     var loading = _this.$loading({
        lock: true,
        text: "Padding....",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.5)",
        stroke: "#c8291c",
      }); 

      var account = _this.CBNpoolInfo.account;
      this.CBNpool.methods
        .getReward()
        .send({ from: account })
       .then((res) => {
          console.log(res);
          loading.close();
          this.$message({
            showClose: true,
