    //取消抵押
    withdraw: function () {
     var _this = this;
      if (_this.CBNpoolInfo == null) {
        _this.$message({
          showClose: true,
          message: _this.$t("message.nolLinked"),
          type: "error",
        });
        return;
      }

      var account = _this.CBNpoolInfo.account;
      var loading = _this.$loading({
        lock: true,
        text: "Padding....",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.5)",
      });
      console.log(_this.CBNpoolInfo.info[6])
      this.CBNpool.methods
        .withdraw(_this.CBNpoolInfo.info[6])
        .send({ from: account })
        .then((res) => {
          console.log(res);
          loading.close();
          this.$message({
            showClose: true,
            message: "success",
            type: "success",
          });
           _this.add_CBNpool();
           _this.add_CbnCoinContract();
        })
        .catch((err) => {
          loading.close();
          _this.$message({
            showClose: true,
            message: err.message,
            type: "error",
          });
          console.log(err);
        });
    },
     //查询白名单
    queryList: function () {
     var _this = this;
     _this.listTileTxt = "";
      if (_this.CBNpoolInfo == null) {
        _this.$message({
          showClose: true,
          message: _this.$t("message.nolLinked"),
          type: "error",
        });
        return;
      }

      var account = _this.CBNpoolInfo.account;
      var loading = _this.$loading({
        lock: true,
        text: "Padding....",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.5)",
      });
      if(!_this.WhiteListArr){
        _this.$message({
          showClose: true,
          message: '请输入需要查询的地址',
          type: "error",
        });
        return;
      }
      this.CBNpool.methods
        .isWhiteList(_this.WhiteListArr)
        .call({ from: account })
        .then((res) => {
          _this.listTile = true;
          if(res){
            _this.listTileTxt = true;
          }else{
            _this.listTileTxt = false;
          }
          console.log(res);
          loading.close();
          // this.$message({
          //   showClose: true,
          //   message: "success",
          //   type: "success",
          // });
          //  _this.add_CBNpool();
          //  _this.add_CbnCoinContract();
        })
        .catch((err) => {
          loading.close();
           _this.listTile = false;
           _this.listTileTxt = '';
          _this.$message({
            showClose: true,
            message: err.message,
            type: "error",
          });
          console.log(err);
        });
    },
  },
};
